// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import 'animate.css'
import 'font-awesome.css'
import Vivus from 'vivus'
import imagesLoaded from 'imagesLoaded'
import { TweenMax, Power2 } from 'gsap'
import ScrollToPlugin from 'gsap/ScrollToPlugin'
import Typed from 'typed.js'
import ScrollMagic from 'scrollmagic'
import 'imports-loader?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap'
Vue.config.productionTip = false
/* eslint-disable no-new */
var v = new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  methods: {
    initScroll: function () {
      var controller = new ScrollMagic.Controller()
      new ScrollMagic.Scene({ triggerElement: '#one' }).addTo(controller).setClassToggle('#box1', 'movein')
      new ScrollMagic.Scene({ triggerElement: '#two' }).setClassToggle('#box2', 'movein').addTo(controller)
      var $article = document.getElementById('work').getElementsByClassName('article')
      var workTween = TweenMax.staggerTo($article, 2, { left: 0, ease: Power2.easeOut }, 0.2)
      new ScrollMagic.Scene({ triggerElement: '#work' }).setTween(workTween).addTo(controller)
    },
    initLoader: function () {
      var l = new Vivus('logo-icon', {
        onReady: function (myVivus) {
          myVivus.el.setAttribute('display', 'block')
        },
        type: 'oneByOne',
        duration: 10,
        animTimingFunction: Vivus.EASE
      }, function () {
        l.stop().reset().play(1)
      })
      var imgLoad = new imagesLoaded(document.getElementById('app'), {
        background: true
      })
      imgLoad.on('always', function (instance) {
        console.log('all images loaded')
      })
      imgLoad.on('done', function (instance) {
        console.log('all images successfully loaded')
        if (l.getStatus() === 'progress') {
          l.callback = function () {
            l.stop()
            window.scrollTo(0, 0)
            TweenMax.fromTo('.st0', 1, { fill: '#FFF' }, {
              fill: '#000',
              onComplete: function () {
                TweenMax.to('#header', 0.5, {
                  height: '3rem',
                  onComplete: function () {
                    document.getElementById('navbar').classList.add('cur')
                    document.getElementById('toggle').classList.add('cur')
                    // that.initType()
                  }
                })
                TweenMax.to(document.getElementById('loader'), 0.5, {
                  height: '2.4rem',
                  margin: 'auto 0.5rem'
                })
              }
            })
          }
        }
      })
      imgLoad.on('fail', function (instance) { console.log('all images loaded, at least one is broken') })
    },
    initType: function () {
      new Typed('.sayhello', {
        strings: ['<h2>Aloha!</h2>'],
        typeSpeed: 30,
        loop: false,
        onComplete: function () {
          document.getElementsByClassName('typed-cursor')[0].style.display = 'none'
          new Typed('.saywelcome', {
            strings: ['<p>Welcome to <b>My Website!<b></p>'],
            typeSpeed: 50,
            loop: false,
            onComplete: function () {
              document.getElementsByClassName('fa-hand-o-down')[0].style.display = 'block'
            }
          })
        }
      })
    }
  }
})
v.initLoader()
v.initType()
v.initScroll()



